.tagList {
  margin: 10px 0 20px 0; }

.tagList__item {
  display: inline-block;
  height: 28px;
  line-height: 28px;
  padding: 0 14px;
  border: 1px solid #f2f2f4;
  border-radius: 14px;
  margin-right: 4px;
  margin-top: 10px;
  color: #333;
  font-size: 14px; }

.tagList__item--selected {
  border: 1px solid #ec5b68; }



