.detail__top {
  background-color: #d4b267;
  padding: 0 20px; }

.detail__content {
  padding: 0 20px 40px; }

.detail__module {
  padding: 14px 0; }
  .detail__module:nth-child(n+2) {
    border-top: 1px solid #ccc; }

.detail__overview {
  color: #333;
  font-size: 14px;
  line-height: 28px; }

.detail .buybtn {
  position: fixed;
  height: 55px;
  width: 100%;
  line-height: 55px;
  color: #fff;
  text-align: center;
  bottom: 0;
  background-color: #ff7ba0;
  text-decoration: none; }



