.seat {
  padding: 0; }
  .seat .tOperator {
    padding: 0 20px; }
  .seat__tip {
    height: 30px;
    padding: 10px 0;
    background: url("../../icon/seats.svg") 50% 50%/auto 30px no-repeat; }
  .seat__graph {
    background-color: #f7f7f7;
    width: 100%;
    height: 500px;
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch; }
  .seat__screen {
    font-size: 16px;
    height: 28px;
    line-height: 28px;
    color: #000;
    text-align: center;
    background: transparent url("../../icon/screen.svg") 50% 0/auto 28px no-repeat; }



