.slide {
  height: 100%;
  overflow: hidden; }
  .slide img {
    width: 100%;
    height: 100%; }
  .slide__dots {
    position: absolute;
    width: 100%;
    bottom: 0;
    text-align: center;
    margin: 0;
    padding: 0; }
    .slide__dots li {
      display: inline-block; }
      .slide__dots li::before {
        content: '';
        display: block;
        width: 8px;
        height: 8px;
        background-color: rgba(255, 255, 255, 0.5);
        margin: 4px 4px; }
      .slide__dots li.slick-active::before {
        background-color: #FFF; }
      .slide__dots li button {
        display: none; }



